import React from "react";
import FullScreenSection from "./FullScreenSection";
import {Box,Heading} from "@chakra-ui/react";
import Card from "./Card";
const projects=[
    {
        title:"Restaurant App",
        url:"https://github.com/kumarshrivastav/django-restaurant-app",
        description:"Restaurant App Designed in the Django. It is use for Table Management System and Adminstative Panel to assigned Privillege.",
        getImageSrc:()=>require("../images/Screenshot (375).png"),
    },
    {
        title:"Todo App",
        url:"http://kumarshrivastav.github.io/react-todo",
        description:"This is a Todo App which designed in React JS. It's allow you to Add Task, Update Task, Complete Task and Delete Task.",
        getImageSrc:()=>require("../images/Screenshot (376).png"),
    },
    {
        title:"Country's Info App",
        url:"https://github.com/kumarshrivastav/xhr-country",
        description:"This is Plain Vanila Javascript App. It's App used the external APIs to Fetch the Data of All the Countries which one you select.",
        getImageSrc:()=>require("../images/Screenshot (256).png"),
    },
    {
      title:"Weather App",
      url:"http://kumarshrivastav.github.io/weather-app",
      description:"This is a React JS application. It's App used the external APIs to Fetch the Data of all the cities .", 
      getImageSrc:()=>require("../images/Screenshot (414).png"),
    },
];

const ProjectsSection =()=>{
    return (
        <FullScreenSection
        isDarkBackground
        backgroundColor="#14532d"
        p={8}
        alignItems="flex-start"
        spacing={8}
        >
            <Heading as="h1" id="projects-section">Featured Projects</Heading>
            <Box display="grid"
                gridTemplateColumns="repeat(2,minmax(0,1fr))" gridGap={8}>
                {projects.map((project)=>(
                    <Card key={project.title}
                    title={project.title}
                    url={project.url}
                    description={project.description}
                    imageSrc={project.getImageSrc()}/>
                ))}
            </Box>
        </FullScreenSection>
    );
};
export default ProjectsSection;